import React, { Component } from 'react';
import OneImage from '../images/1.png';
import TwoImage from '../images/2.png';
import ThreeImage from '../images/3.png';
import FourImage from '../images/4.png';

import OneImager from '../images/odyn.png';
import TwoImager from '../images/dwa.png';
import ThreeImager from '../images/try.png';
import FourImager from '../images/chet.png';


class Article extends Component {
  render() {
    return (
      <React.Fragment>
      <h2>Article</h2>
      <div style={{display:"flex", justifyContent:"space-around",alignItems:"center",width:"1440px",maxWidth:"100%",minHeight:"170px",background:'white'}}>
        <p><img src={OneImage} alt="#" title="#"/><span>Доставка</span></p>
        <p><img src={TwoImage} alt="#" title="#"/><span>Доставка</span></p>
        <p><img src={ThreeImage} alt="#" title="#"/><span>Доставка</span></p>
        <p><img src={FourImage} alt="#" title="#"/><span>Доставка</span></p>
      </div>
      <div style={{display:"flex", justifyContent:"center",alignItems:"center",width:"1440px",maxWidth:"100%",flexWrap:"wrap"}}>
      <img src={OneImager} alt="#" title="#"/>
        <img src={TwoImager} alt="#" title="#"/>
        <img src={ThreeImager} alt="#" title="#"/>
        <img src={FourImager} alt="#" title="#"/>
      </div>
      <div style={{display:"flex", justifyContent:"space-around",alignItems:"center",width:"1440px",maxWidth:"100%",borderRadius:"20px",minHeight:"500px",background:"rgba(255,0,0,0.1)"}}>Cart 1</div>
      <div style={{display:"flex", justifyContent:"space-around",alignItems:"center",width:"1440px",maxWidth:"100%",borderRadius:"20px",minHeight:"500px",background:"rgba(0,255,0,0.1)"}}>Cart 2</div>
      <div style={{display:"flex", justifyContent:"space-around",alignItems:"center",width:"1440px",maxWidth:"100%",borderRadius:"20px",minHeight:"500px",background:"rgba(0,0,255,0.1)"}}>Cart 3</div>
      <div style={{display:"flex", justifyContent:"space-around",alignItems:"center",width:"1440px",maxWidth:"100%",borderRadius:"20px",minHeight:"500px",background:"rgba(255,255,0,0.1)"}}>Cart 4</div>
      <div style={{display:"flex", justifyContent:"space-around",alignItems:"center",width:"1440px",maxWidth:"100%",borderRadius:"20px",minHeight:"500px",background:"rgba(0,255,255,0.1)"}}>Cart 5</div>
      </React.Fragment>
    );
  }
}

export default Article;



